import { Component, OnInit }  from '@angular/core';
import { ActivatedRoute }     from '@angular/router'
import { FormBuilder, 
  FormArray, 
  FormGroup, 
  FormControl,
  Validators }                from '@angular/forms'

import { FirebaseMOSService } from 'app/shared/firebaseMOS.service'
import { PostDetails }        from '../post'
import { PostItemDetails }    from '../postItemDetails'
import { FileUploaderService }  from 'app/shared/file-uploader.service'
import { SpinnerComponent }   from 'app/shared/spinner.component'
import { BasicValidators }    from 'app/shared/basicValidators'
import { PostItemsListComponent } from 'app/shared/post-items-list/post-items-list.component'

@Component({
  selector: 'mos-postdetails',
  template: require('./post-details.component.html')
})
export class PostDetailsComponent implements OnInit {
  itemDetailsForm : FormGroup;
  selectedPostKey:string;
  selectedPost:PostDetails;
  tempPostItemDetail:PostItemDetails;
  addItem:boolean;
  items:PostItemDetails[]=[];
  file_srcs: string[] = [];
  selectedImages:any;
  onSubmit:boolean;
  onSubmitProgress:string;
  displayItems:boolean=true;

  constructor(private route:ActivatedRoute,
              private fb:FormBuilder,
              private af:FirebaseMOSService,
              private fu:FileUploaderService) {}

  ngOnInit() {
    this.onSubmit = false;
    this.fu.filesChanged.subscribe(f=>this.file_srcs=f);
    this.fu.filesUploaded
      .subscribe(r=>this.postItem(r));

    this.selectedPostKey = this.route.snapshot.params['id'];
    this.af.getPost(this.selectedPostKey)
      .subscribe(o=>this.selectedPost=o[0]);

    this.af.getPostItems(this.selectedPostKey)
      .subscribe(o=>this.items=o);

    this.itemDetailsForm = this.fb.group({
        description:['',Validators.required],
        price:['',Validators.required],
        year:['',[Validators.required,BasicValidators.validYear]],
        pictureURL:[''],
        externalLink:['']
    });
    this.addItem=false;
    this.tempPostItemDetail = new PostItemDetails();
    this.tempPostItemDetail.externalLink=" ";
  }

  onCancel(){
    this.addItem  = this.onSubmit = false;
    this.file_srcs = [];
    this.selectedImages = null;
    this.displayItems = true;
  }

  onNewItem(){
    this.addItem = true;
    this.displayItems = false;
    this.tempPostItemDetail = new PostItemDetails();
  }

  onEditItem(item){
    this.addItem = true;
    this.displayItems = false;
    this.tempPostItemDetail = item;
    this.file_srcs = item.pictureURL;
  }

  onDeleteItem(item){
    this.addItem = true;
    this.displayItems = false;
    this.tempPostItemDetail = item;
    this.file_srcs = item.pictureURL;
    console.log("onDeleteItem");
  }

  onAddItem(){
    this.onSubmit = true; 
    this.tempPostItemDetail = new PostItemDetails();
    // Assign post details
    this.tempPostItemDetail.postId = this.selectedPost.$key;
    this.tempPostItemDetail.location = this.selectedPost.location;
    this.tempPostItemDetail.locationCity = this.selectedPost.locationCity;
    this.tempPostItemDetail.locationState = this.selectedPost.locationState;
    this.tempPostItemDetail.locationZipCode = this.selectedPost.locationZipCode;
    
    // Assign item details
    this.tempPostItemDetail.description = this.itemDetailsForm.value.description;
    this.tempPostItemDetail.pictureURL  = "";
    this.tempPostItemDetail.price       = this.itemDetailsForm.value.price;
    this.tempPostItemDetail.year        = this.itemDetailsForm.value.year;
    if(this.itemDetailsForm.value.externalLink)
      this.tempPostItemDetail.externalLink  = this.itemDetailsForm.value.externalLink;
    else
      this.tempPostItemDetail.externalLink  = "";

    //upload images and add item with reference to images
    if(this.selectedImages){
      this.onSubmitProgress = "Uploading Images...";
      this.fu.uploadImages(this.selectedImages);
    }else {
      this.postItem("");    
    }
  }

  postItem(imageUrls){
    if(this.onSubmit){
      console.log("Files uploaded to Firebase."+JSON.stringify(imageUrls)); 
      this.onSubmitProgress = "Uploading Item details...";
      this.tempPostItemDetail.pictureURL  = imageUrls;

      this.af.addPostItem(this.tempPostItemDetail)
        .then(res=>{
                    console.log(res);
                  },
              error=>console.log(error));
      this.file_srcs = [];
      this.selectedImages = null;
      this.onSubmit = false;
      this.addItem  = false;
      this.displayItems = true;
    }
  }

  displayImages(input){
    console.log("file change");
    console.log(input.files);
    this.selectedImages = input.files;
    this.fu.displayImages(input);
  }
}



